body{
    background-color: #fafafa;
	padding: 60px;
	font-family: 'Droid Sans', sans-serif;
}

#container {
    display: flex;
    justify-content: flex;
}

.card-mb-3 {
    background: #ffffff;
	border: 1px solid #e2dfdf;
	border-radius: 3px;
	width: 100%;
	max-width: 600px;
    margin-left: 300px;
    
}

.card-body{
	padding: 30px;
	height: 40px;
}

.card-post {
    border-radius: 50%;
    height: 40px;
    width: auto;
    vertical-align: middle;
    padding-bottom: 800px;
    color: #999;
}

.card-text {
    float: right;
	width: 40px;
	padding-top:10px;
	text-align: right;
    color: #262626;
    padding-right: 30px;
    text-transform: capitalize;
    
}

.card-title {
	font-weight:bold;
    color: #262626;
}

